<script>
  import Schedulely from "@/js/schedulely/schedulely";
  import Events from "./Events";
  import Avatar from "vue-avatar";
  import { bus } from "@/js/core/emit-bus";
  export default {
    name: "Users",
    components: { Events, Avatar },
    data() {
      return {
        domainId: "",
        userId: "",
        eventId: "",
        usersList: [],
        user: [],
        eventsList: [],
        event: [],
        eventExists: false,
        userExists: false,
        eventShow: false,
        isSlots: false,
        isUserDetails: false,
        previousIndex: 0,
        isBusy: false,
        ShowAgent: false,
        projectId: "",
        projectList: [],
        selectedProject: {},
        reRender: 0,
        customClass: false,
      };
    },
    async mounted() {
      let shortcutData;
      this.isBusy = true;
      let host = window.location.host;
      if (host.includes(".schedule.cc")) {
        shortcutData = await Schedulely.getShortcutData(host.split(".schedule.cc")[0]);
        if (!shortcutData || !shortcutData.length) return;
        window.domain = shortcutData[0].domain_id;

        // Set project_id and id if the shortcut url type is event level
        if (shortcutData[0].link == "event_level") {
          if (shortcutData[0].data_1) {
            window.project_id = JSON.parse(shortcutData[0].data_1).event_type.split("-")[0];
            window.id = JSON.parse(shortcutData[0].data_1).event_type.split("-")[1];
          }
        }

        // Set project_id and id as null if the shortcut url type is not an event level
        else {
          window.project_id = shortcutData[0].data_1 ? JSON.parse(shortcutData[0].data_1).project_id : null;
          window.id = null;
        }
        window.region = shortcutData[0].env;
        window.user = shortcutData[0].link == "user" || shortcutData[0].link == "event_level" ? shortcutData[0].created_by : null;
      }
      // Get Domain Id and User Id
      this.domainId = window.domain;
      this.userId = window.user;
      this.eventId = window.id;
      this.projectId = window.project_id;
      this.isBusy = true;
      let region = window.region;
      if (this.projectId) {
        this.projectList = await Schedulely.get(`project/${this.domainId}?project_id=${this.projectId}`);
        if (this.projectList && this.projectList.length > 0) {
          this.selectedProject = this.projectList[0];
          if (!this.userId) await this.getProjectUsers(this.projectList[0], 0);
        }
        this.isSlots = false;
        this.isUserDetails = false;
        // User Exists
        if (this.userId) {
          this.ShowAgent = true;
          this.usersList = await Schedulely.get_agents(region, `project/${this.domainId}/${this.projectId}?project_name=${this.selectedProject.name}`);
          this.user = this.usersList.filter((user) => user.id == this.userId);
          this.user = this.user[0];
          this.user["selecteduser"] = true;
          this.userExists = true;
          this.isSlots = false;
          this.isUserDetails = false;
          // Get single event if event id present
          if (this.eventId) {
            this.customClass = true;
            this.eventExists = true;
            this.eventsList = await Schedulely.get(`${this.domainId}/${this.userId}/${this.eventId}`);
            this.getEventDetails(this.eventsList[0], 0);
          } else {
            this.eventsList = await Schedulely.get(`project/${this.domainId}/${this.projectId}/${this.userId}?project_name=${this.selectedProject.name}`);
            this.eventShow = true;
          }
          if (this.user) this.eventShow = true;
        } else this.customClass = false;
      }
      this.isBusy = false;
      bus.$on("updateCustomClass", () => {
        this.customClass = false;
      });
    },
    methods: {
      async getProjectUsers(project) {
        this.isBusy = true;
        this.eventShow = false;
        this.isSlots = false;
        this.selectedProject = project;
        this.projectId = project.id;
        this.usersList = await Schedulely.get_agents(window.region, `project/${this.domainId}/${this.projectId}?project_name=${this.selectedProject.name}`);
        if (this.usersList && this.usersList.length == 1) this.getEvents(this.usersList[0], 0);
        this.ShowAgent = true;
        this.isBusy = false;
      },
      // Get Events by User
      async getEvents(userData, index) {
        this.eventShow = false;
        this.isSlots = false;
        // Unselecting the previously selected divs
        if (this.usersList[this.previousIndex] && this.usersList[this.previousIndex].selecteduser) this.usersList[this.previousIndex].selecteduser = false;
        // To activate the selected div
        this.usersList[index].selecteduser = true;
        // Saving the old index
        this.previousIndex = index;
        this.user = userData;
        this.isSlots = false;
        this.isUserDetails = false;
        this.eventsList = await Schedulely.get(`project/${this.domainId}/${this.projectId}/${this.user.id}?project_name=${this.selectedProject.name}`);
        this.eventShow = true;
      },
      // Get Event Details by Event
      getEventDetails(eventData, index) {
        this.reRender += 1;
        this.event = eventData;
        this.isSlots = true;
        this.isUserDetails = false;
        this.eventsList.map((item) => {
          item.selectedevent = false;
        });
        this.eventsList[index].selectedevent = true;
        //this.isEventDetailsLoaded = true;
        if (this.$refs.eventDetailsRef) {
          this.$refs.eventDetailsRef.getEventData(this.event.id);
          this.$refs.eventDetailsRef.timesArray = [];
        }
      },
      openPopover() {
        this.$refs.popover.$emit("open");
      },
      showSteps(type) {
        if (type == "user") {
          this.showUser = true;
          this.eventShow = false;
          this.isSlots = false;
          this.isUserDetails = false;
        } else if (type == "event" && this.user && this.user.email) {
          this.showUser = true;
          this.eventShow = true;
          this.isSlots = false;
          this.isUserDetails = false;
        } else if (type == "slot" && this.event && this.event.title) {
          this.isSlots = true;
          this.showUser = true;
          this.eventShow = true;
          this.$refs.eventDetailsRef.isUserDetails = false;
          this.$refs.eventDetailsRef.isUserData = true;
          this.isUserDetails = false;
        } else if (type == "booking") {
          this.$refs.eventDetailsRef.showBooking("booking");
        }
      },
    },
  };
</script>
